
.header {
  box-sizing: border-box;
  width: 100%;
  padding-left: 27px;
  padding-bottom: 30px;
  padding-top: 28px;
  border-bottom: 1px solid #545454;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 1024px) {
  .header {
    padding-left: 0px;
    padding-top: 45px;
    padding-bottom: 41px;
  }
}
